import Vue from 'vue'
import Router from 'vue-router'
import Login from '../views/Login.vue'
import Welcome from '../views/Welcome'
import Thanks from '../views/Thanks'
import NoPage from '../views/404'

import Layout from "../views/Layout/Layout";
const _import = require('@/router/_import_' + process.env.NODE_ENV)//

Vue.use(Router)

const createRouter = () => new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/404', component: NoPage, name: 'NoPage',
            meta: {
                title: 'NoPage',
                requireAuth: false,
                NoTabPage: true,
                NoNeedHome: true // home
            },
            hidden: true
        },
        {
            path: '/Thanks', component: Thanks, name: 'Thanks',
            meta: {
                title: 'Thanks',
                requireAuth: false
            },
            hidden: true
        },
        {
            path: '/',
            component: Welcome,
            name: 'QQ',
            iconCls: 'fa-qq',//class
            // hidden: true,
            meta: {
                title: 'QQ',
                requireAuth: true // 
            }
        },
        {
            path: '/login',
            component: Login,
            name: 'login',
            iconCls: 'fa-address-card',//class
            meta: {
                title: '',
                NoTabPage: true,
                NoNeedHome: true // home
            },
            hidden: true
        },
        {
            path: '*',
            hidden: true,
            redirect: {path: '/404'}
        }
    ]
})

const router = createRouter()

export function  filterAsyncRouter(asyncRouterMap) {
    // asyncRouterMap 
    const accessedRouters = asyncRouterMap.filter(route => {
        if (route.path) {
            if (route.path === '/'||route.path === '-') {//Layout
                route.component = Layout
            } else {
                try {
                    route.component = _import(route.path)
                }catch (e) {
                    console.info('%c  '+route.path+'.vue !', "color:red")

                }
            }
        }
        if (route.children && route.children.length) {
            route.children = filterAsyncRouter(route.children)
        }
        return true
    })

    return accessedRouters
}

export function resetRouter () {
    const newRouter = createRouter()
    router.matcher = newRouter.matcher // the relevant part
}

export default router;

